h1, h2, h3, h4, h5, h6, p {
   font-family: 'Montserrat', sans-serif;
}

.weather-widget {
  font-family: Arial, sans-serif;
  padding: 10px;
  border-radius: 8px;
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.widget-location {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 20px;
}

.widget-daily {
  display: flex;
  justify-content: center;
  gap: 15px;
}

.daily-weather {
  background: linear-gradient(163.15deg, rgba(96, 114, 138, 0.9) -1.62%, rgba(25, 30, 36, 0.9) 112.31%);
  border: 1px solid transparent;
  border-image-source: linear-gradient(170.97deg, rgba(153, 153, 153, 0) -1.23%, rgba(255, 255, 255, 0.1) 108.74%);
  border-image-slice: 1; /* Required for border-image to work */
  border-radius: 8px;
  width: 166px;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
}

.day-header {
  background: white; 
  padding: 5px 0;
  width: 100%;
}

.widget-day {
  font-size: 1rem;
  font-weight: bold;
  margin: 0;
  background-image: linear-gradient(216.32deg, #60728A 38.46%, #191E24 112.02%);
  color: transparent;
  background-clip: text;
}

.daily-content {
   width: 100%;
  height: -webkit-fill-available;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 8px;
}

.widget-icon {
  width: 105px;
  height:100px;
  margin: 10px 0;
}

.widget-temp-high {
  font-size: 24px;
  margin: 5px 0;
  color: #FFF; /* Highlight high temperature */
  font-weight: 700;
}

.widget-feels-like-high {
  font-size: 16px;
  margin: 5px 0;
  color: #FFF; /* Highlight low temperature */
  font-weight: 600;
}

.widget-summary{
  color: #FFF;
  font-size: 15px;
  font-weight: 500;
  min-height: 45px;
  max-width: 90%;
  text-align: center;
}

.widget-windRain-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 100%; 
  gap: 40px; 
}

.wind-box, .precip-box {
  display: flex;
  flex-direction: column;
  flex: 1; 
}
.wind-box{
  align-items: flex-start;
  justify-content: start;
}

.widget-wind-direction, .widget-precip-percent {
   white-space: nowrap; /* Prevents wrapping */
  overflow: hidden;
  font-weight: 500;
  font-size: 12px;
  color: #FFF;
}

.precip-box {
  align-items: flex-end;
  justify-content: end;
}

/* Hourly */
.widget-hourly {
  display: flex;
  justify-content: center;  
  gap: 15px;
  flex-wrap: nowrap;
  margin-top: 20px;
  overflow-x: auto;
}

.hourly-weather {
  background: linear-gradient(163.15deg, rgba(96, 114, 138, 0.9) -1.62%, rgba(25, 30, 36, 0.9) 112.31%);
  border: 1px solid transparent;
  border-image-slice: 1;
  border-radius: 8px;
  width: 166px;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
}

.hourly-header {
  background: white;
  padding: 5px 0;
  width: 100%;
}

.widget-hour {
  font-size: 1rem;
  font-weight: bold;
  margin: 0;
  background-image: linear-gradient(216.32deg, #60728A 38.46%, #191E24 112.02%);
  color: transparent;
  background-clip: text;
}

.hourly-content {
  width: 100%;
  height: -webkit-fill-available;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 8px;
}

.widget-temp {
  font-size: 24px;
  margin: 5px 0;
  color: #FFF;
  font-weight: 700;
}

.widget-feels-like {
  font-size: 16px;
  margin: 5px 0;
  color: #FFF;
  font-weight: 600;
}

.widget-precipitation {
  font-weight: 600;
  font-size: 1rem;
  color: #FFF;
  margin: 5px 0;
}

/* Testing Dropdowns */
.testing-dropdowns{
  display: flex;
  align-items: center;
  justify-content: center;
}

.type-parent{
  margin: 0 10px 0 3px;
}

.duration-parent{
  margin: 0 10px 0 3px;
}

.type-label{
  margin: 0 5px;
}
.duration-label{
 margin: 0 5px;
}


/* Responsive Adjustments */
@media (max-width: 768px) {
  .daily-weather, .hourly-weather {
    max-width: 140px; /* Scale down for medium screens */
  }

  .widget-location {
    font-size: 1.2rem;
  }

  .widget-temp-high, .widget-temp {
    font-size: 20px;
  }

  .widget-feels-like-high, .widget-feels-like {
    font-size: 14px;
  }

  .widget-summary {
    font-size: 14px;
    max-width: 95%;
  }
}

@media (max-width: 600px) {
  .widget-daily, .widget-hourly {
    gap: 5px;
  }

  .daily-weather, .hourly-weather {
    max-width: 150px; /* Shrinks further for very small screens */
  }

  .widget-day{
    font-size: 7px;
    flex-wrap: wrap;
  }

  .widget-icon {
    width: 80%;
    height: 80%;
  }

  .widget-temp-high, .widget-temp {
    font-size: 10px;
  }

  .widget-feels-like-high, .widget-feels-like {
    padding: 0 5px;
    font-size: 7px;
  }

  .widget-summary {
    font-size: 7px;
    min-height: 30px;
  }

  .widget-windRain-box {
    flex-direction: column;
    align-items: center;
    gap: 5px;
  }
  .wind-box, .precip-box {
    word-wrap: normal;
    align-items: center;
    padding: 0 5px;
  }

  .wind-detail-icon, .rain-detail-icon{
    width: 10px;
    height: 10px;
  }
  .widget-wind-direction, .widget-precip-percent {
    font-size: 7px;
    white-space: normal; 
    text-align: center;
  }
}



@media (max-width: 400px) {
  .widget-daily, .widget-hourly {
    gap: 5px;
  }

  .daily-weather, .hourly-weather {
    max-width: 120px; /* Shrinks further for very small screens */
  }

  .widget-day{
    font-size: 7px;
    flex-wrap: wrap;
  }

  .widget-icon {
    width: 80%;
    height: 80%;
  }

  .widget-temp-high, .widget-temp {
    font-size: 10px;
  }

  .widget-feels-like-high, .widget-feels-like {
    padding: 0 5px;
    font-size: 7px;
  }

  .widget-summary {
    font-size: 7px;
    min-height: 30px;
  }

  .widget-windRain-box {
    flex-direction: column;
    align-items: center;
    gap: 5px;
  }
  .wind-box, .precip-box {
    word-wrap: normal;
    align-items: center;
    padding: 0 5px;
  }

  .wind-detail-icon, .rain-detail-icon{
    width: 10px;
    height: 10px;
  }
  .widget-wind-direction, .widget-precip-percent {
    font-size: 7px;
    white-space: normal; 
    text-align: center;
  }
}
